body{
  margin: 0;
  padding: 0;
  background: linear-gradient(
    to left,
    rgba(7,27,82,1)0%, 
    rgba(0,128,128,1)100%
  );
  cursor:pointer;
  text-align: center;
}
.title{
  font-size: 3.5em;
  font-family: 'Macondo';
  color: #95dada;
}
.title:hover{
  color:rgb(2, 247, 255) ;
}
.buttonChange{
  border-radius: 3px;
  outline: none;
  background-color: #8cd7a16a;
  font-size: .5rem;
  color: white;
}
.buttonChange:hover{
  background-color: orange;
}
.card{
  background-color: #95dada;
  width: 25vw;
  margin: 0 auto;
  padding: 10px;
  border-radius: 20px;  
  margin-bottom: 40px;
  transition: transform .5s ease-in-out;
}
.card:hover{
  background-color: rgb(2, 247, 255);
  transform: scale(1.1);
}
.getrandom {
  border-color: #0ff1ed;
  border-radius: 5px;
  color: white;
  background-color: rgba(127, 225, 217, 0.179);
  font-size: 1.4rem;
  background-image: -webkit-linear-gradient(45deg, #88e3dd83 50%, transparent 50%);
  background-image: linear-gradient(45deg, #28c8c6 50%, transparent 50%);
  background-position: 100%;
  background-size: 400%;
  -webkit-transition: background 300ms ease-in-out;
  transition: background .8s ease-in-out;
  font-family: 'Macondo', bold;
}
.getrandom:hover {
  background-position: 0;
  color: white;
  outline:  #00fffb;
}
